document.addEventListener('DOMContentLoaded', function() {

    eventListeners();
    
});



const fars = document.querySelector('.fars');


function eventListeners() {
    
    fars.addEventListener('click', desplegar);
    document.body.addEventListener('click', changeIcon);

}

/* Shows and hide Faqs Answers */
function desplegar() {
    
    const faq_text = document.querySelector('.faq-text');
    faq_text.classList.toggle('show');
    
}

/* Toggle between plus and minus faq icons */
function changeIcon(){
    
    
        const plus_icon = document.querySelector(".fa-plus-square");
        const minus_icon = document.querySelector(".fa-minus-square");
        plus_icon.classList.toggle("hidden");
        minus_icon.classList.toggle("show");
    
    
    
}


